* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

body {
  background-image: linear-gradient( 135deg, #FF7AF5 10%, #513162 100%);
  min-height: 100vh;
  background-size: cover;
}

.row-header{
  margin:0px auto;
  padding:0px auto;
}

.row-content {
  margin:0px auto;
  padding: 50px 0px 50px 0px;
  border-bottom: 1px ridge;
  min-height:400px;
}

.footer{
  background-color: #D1C4E9;
  margin:0px auto;
  padding: 20px 0px 20px 0px;
}
.jumbotron {
  padding:70px 30px 70px 30px;
  margin:0px auto;
  background: #9575CD ;
  color:floralwhite;
}

address{
  font-size:80%;
  margin:0px;
  color:#0f0f0f;
}

.navbar-dark {
  background-color: #512DA8;
}

.jumbotron {
  background: #e8efeb;
  color:#0f3057;
  padding: 8px;
  margin: 0px;
}  

.homeTitle {
  color: #583d72;
  width: 100%;
  margin-bottom: 8px;
  
}  

.homediv {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  align-items: center;
  justify-content:center;
}

.homedata, .homeTitle {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #583d72;
  background: #da9ff9;
  border: #b088f9 solid 4px;
  padding: 7px;
}  

.empdiv {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  align-items: center;
  justify-content: first baseline;
  margin-top: 70px;
}

.app-wrap {
  color: #583d72;
  background: #da9ff9;
  border: #b088f9 solid 4px;
  padding: 7px;
  min-height: 60vh;
  min-width: 50vw;
  margin-left: 25px;
}

.settings-wrap{
  color: #583d72;
  background: #da9ff9;
  border: #b088f9 solid 4px;
  padding: 7px;
  min-height: 60vh;
}

.settingsDiv {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: right;
}

.settingsBtn {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.top 
{
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.middle
{
    border-radius: 0;
    margin-bottom: -1px;
}

.bottom
 {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}